import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/main.vue'
import Article from '../components/mainChild/Article.vue'
import ArticleList from '../components/mainChild/ArticleList.vue'
import Loading from '../components/Loading.vue'
import DashBoard from '../components/DashBoard.vue'
import AllArticle from '../components/DashChild/AllArticle.vue'
import ControlPage from '../components/DashChild/ControlPage.vue'
import Login from '../components/DashChild/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Home,
    children: [{
      path: 'article',
      name: 'article',
      component: Article
    }, {
      path: 'article_list',
      name: 'article_list',
      component: ArticleList
    }, {
      path: 'loading',
      name: 'loading',
      component: Loading
    }]
  }, {
    path: '/dashboard',
    name: 'dashboard',
    component: DashBoard,
    children: [{
      path: 'login',
      name: 'login',
      component: Login
    }, {
      path: 'allarticle',
      name: 'allarticle',
      component: AllArticle
    }, {
      path: 'controlpage',
      name: 'controlpage',
      component: ControlPage
    }]
  }
]

const router = new VueRouter({
  routes
})

export default router
