<template>
  <div class="page-animate">
    <h2>總覽</h2>
    <span class="add-article" @click="goAddArticle()">新增文章</span>
    <div class="container-fluid">
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="poto-group">
            <span>攝影</span>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">文章名稱</th>
                  <th scope="col">文章內容</th>
                  <th scope="col">建立日期</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(poto,index) in potographyCollect"
                  :key="index"
                  class="show-alert show-alert-a"
                  @mousemove="showAlertTitle"
                  @mouseleave="hideAlertTitle"
                  @click="goControl(poto.id,type=1)"
                >
                  <td>{{poto.descripTitle}}</td>
                  <td>{{poto.descripContent}}</td>
                  <td>{{poto.date}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="3">文章總數 {{potographyCollect.length}} 篇</td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="psy-group">
            <span>心理學</span>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">文章名稱</th>
                  <th scope="col">文章內容</th>
                  <th scope="col">建立日期</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(psy,index) in psychologyCollect"
                  :key="index"
                  class="show-alert show-alert-b"
                  @mousemove="showAlertTitle"
                  @mouseleave="hideAlertTitle"
                  @click="goControl(psy.id,type=2)"
                >
                  <td>{{psy.descripTitle}}</td>
                  <td>{{psy.descripContent}}</td>
                  <td>{{psy.date}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="3">文章總數 {{psychologyCollect.length}} 篇</td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="code-group">
            <span>程式語言</span>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">文章名稱</th>
                  <th scope="col">文章內容</th>
                  <th scope="col">建立日期</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(code,index) in codeCollect"
                  :key="index"
                  class="show-alert show-alert-c"
                  @mousemove="showAlertTitle"
                  @mouseleave="hideAlertTitle"
                  @click="goControl(code.id,type=3)"
                >
                  <td>{{code.descripTitle}}</td>
                  <td>{{code.descripContent}}</td>
                  <td>{{code.date}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="3">文章總數 {{codeCollect.length}} 篇</td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="back-btn-group">
      <router-link to="/">
        <span class="back">返回部落格</span>
      </router-link>
    </div>
    <span class="alert-message">可點擊標題修改內容</span>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 0.7s ease;
  overflow: hidden;
  h2 {
    text-align: center;
    color: white;
    font-weight: bold;
  }
  .add-article {
    display: block;
    position: absolute;
    top: 5%;
    right: 0;
    padding: 3px 10px 3px 10px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.7),
      0 0 2px 1px rgba(0, 0, 0, 0.7);
    transform: translate(-50%, 0);
    opacity: 1;
    transition: 0.7s ease;
    color: white;
    cursor: pointer;
    user-select: none;
  }
  .add-article-hide {
    opacity: 0;
    transform: translate(-50%, -100%);
  }
  @media screen and (max-width: 414px) {
    .add-article {
      top: 1%;
      transform: translate(-10%, 0);
    }
    .add-article-hide {
      opacity: 0;
      transform: translate(-10%, -100%);
    }
  }
  .poto-group,
  .psy-group,
  .code-group {
    color: white;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    margin: 10px 0 10px 0;
    box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
      0 0 3px 1px rgba(0, 0, 0, 0.7);
    span {
      display: block;
      text-align: center;
      font-size: 30px;
      font-weight: bold;
      padding: 10px 0 10px 0;
    }
    .table {
      color: white;
      margin-bottom: 0;
      table-layout: fixed;
      thead {
        tr {
          th {
            text-align: center;
          }
        }
      }
      tbody {
        .show-alert-a,
        .show-alert-b,
        .show-alert-c {
          cursor: pointer;
          user-select: none;
          box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0.7);
          text-align: center;
          transition: 0.7s ease;
          td {
            text-align: center;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
        .show-alert-active {
          background-color: rgba(255, 255, 255, 0.7);
          box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7);
          color: black;
        }
      }
      tfoot {
        tr {
          td {
            text-align: right;
          }
        }
      }
    }
  }
  .back-btn-group {
    display: flex;
    justify-content: center;

    .back {
      display: block;
      text-align: center;
      padding: 3px 15px 3px 15px;
      margin: 15px 0 15px 0;
      background-color: rgb(62, 81, 255);
      color: white;
      border-radius: 5px;
      width: 200px;
    }
    a:hover {
      text-decoration: none;
    }
  }
  .alert-message {
    position: absolute;
    display: none;
    opacity: 0;
    transition: 0.5s ease;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 3px 5px 3px 5px;
    border-radius: 5px;
  }
  .alert-message-active {
    display: block;
    opacity: 1;
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      articles: [],
      articlesCollection: [],
      potographyCollectTemp: [],
      potographyCollect: [],
      psychologyCollectTemp: [],
      psychologyCollect: [],
      codeCollectTemp: [],
      codeCollect: [],
      showAlertCountOrigin: -1,
      showAlertCountA: -1,
      showAlertCountB: -1,
      showAlertCountC: -1,
      currentIndex: 0,
    };
  },
  mounted() {
    setTimeout(
      () =>
        document
          .querySelector(".page-animate")
          .classList.add("page-animate-active"),
      800
    );
    setTimeout(() => {
      document
        .querySelectorAll(".show-alert")
        .forEach((key) =>
          key.setAttribute("data-num", (this.showAlertCountOrigin += 1))
        );
      document
        .querySelectorAll(".show-alert-a")
        .forEach((key) =>
          key.setAttribute("data-post", (this.showAlertCountA += 1))
        );
      document
        .querySelectorAll(".show-alert-b")
        .forEach((key) =>
          key.setAttribute("data-post", (this.showAlertCountB += 1))
        );
      document
        .querySelectorAll(".show-alert-c")
        .forEach((key) =>
          key.setAttribute("data-post", (this.showAlertCountC += 1))
        );
    }, 2000);
  },
  methods: {
    showAlertTitle(e) {
      this.currentIndex = e.target.parentNode.dataset.post;
      document.querySelectorAll(".show-alert").forEach((key) => {
        parseInt(key.dataset.num) == parseInt(e.target.parentNode.dataset.num)
          ? key.classList.add("show-alert-active")
          : key.classList.remove("show-alert-active");
      });
      document
        .querySelector(".alert-message")
        .classList.add("alert-message-active");
      document.querySelector(".alert-message").style.top = `${e.pageY}px`;
      document.querySelector(".alert-message").style.left = `${e.pageX + 20}px`;
    },
    hideAlertTitle(e) {
      document
        .querySelectorAll(".show-alert")
        .forEach((key) =>
          parseInt(key.dataset.num) == parseInt(e.target.dataset.num)
            ? key.classList.remove("show-alert-active")
            : null
        );
      document
        .querySelector(".alert-message")
        .classList.remove("alert-message-active");
      document.querySelector(".alert-message").style.top = "";
      document.querySelector(".alert-message").style.left = "";
    },
    goAddArticle() {
      setTimeout(() => {
        const add = true;
        this.$router.push({ name: "controlpage", params: { add } });
      }, 700);
      document.querySelector(".add-article").classList.add("add-article-hide");
    },
    goControl(id, type) {
      let currentIndexPost = this.currentIndex;
      this.$router.push({
        name: "controlpage",
        params: { id, type, currentIndexPost },
      });
    },
    getArticle() {
      const unlock = "https://cors-anywhere.herokuapp.com/";
      const potoUrl = "https://project-50101.firebaseio.com/potography.json/";
      const psyUrl = "https://project-50101.firebaseio.com/psychology.json/";
      const codeUrl = "https://project-50101.firebaseio.com/code.json/";
      this.axios
        .get(`${unlock}${potoUrl}`)
        .then((res) => {
          this.potographyCollectTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.potographyCollectTemp.forEach((key) =>
            key.descripTitle != "" ? this.potographyCollect.push(key) : null
          );
        })
        .catch((err) => console.log(err));
      this.axios
        .get(`${unlock}${psyUrl}`)
        .then((res) => {
          this.psychologyCollectTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.psychologyCollectTemp.forEach((key) =>
            key.descripTitle != "" ? this.psychologyCollect.push(key) : null
          );
        })
        .catch((err) => console.log(err));
      this.axios
        .get(`${unlock}${codeUrl}`)
        .then((res) => {
          this.codeCollectTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.codeCollectTemp.forEach((key) =>
            key.descripTitle != "" ? this.codeCollect.push(key) : null
          );
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.getArticle();
  },
};
</script>