<template>
  <div class="container-fluid">
    <div class="login-outer">
      <div class="login">
        <div class="login-header">
          <span>請先登入</span>
        </div>
        <div class="login-body">
          <div class="account-group">
            <label for="accout">帳號</label>
            <input type="text" id="accout" v-model="accountInput" />
          </div>
          <div class="password-group">
            <label for="password">密碼</label>
            <input type="password" id="password" v-model="passwordInput" />
          </div>
        </div>
        <div class="login-footer" @click="goArticleControlCenter">登入</div>
      </div>
    </div>
    <div class="modalC">
      <div class="modal-bodyC">
        <div class="modal-headerC">
          <span>提示訊息</span>
        </div>
        <div class="modal-contentC">
          <p v-if="loginStatus == true">
            登入成功
            <br />
            {{modalCount}} 秒後將跳轉到文章管理頁面
          </p>
          <p v-else>
            登入失敗
            <br />
            {{modalCount}} 秒後將跳回首頁
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.container-fluid {
  .login-outer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: white;
    margin: 20px auto 70px auto;
    .login {
      min-width: 400px;
      box-shadow: inset 0 0 3px 2px rgba(255, 255, 255, 0.7),
        0 0 2px 1px rgba(0, 0, 0, 0.7);
      overflow: hidden;
      border-radius: 15px 0 15px 0;
      .login-header {
        background-color: rgba(0, 0, 0, 0.7);
        padding: 3px 0 5px 0;
        border-bottom: 2px solid rgba(255, 255, 255, 0.7);
        span {
          display: block;
          padding: 10px 10px 5px 10px;
        }
      }
      .login-body {
        background-color: rgba(0, 0, 0, 0.7);
        padding: 20px 5px 20px 5px;
        .account-group,
        .password-group {
          padding: 5px 10px 5px 10px;
          label {
            display: block;
          }
          input[type="text"],
          input[type="password"] {
            text-align: center;
            border: none;
            outline: none;
            border-radius: 5px;
            width: 100%;
            display: block;
            box-shadow: inset 0 0 0 0 rgba(0, 81, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
            transition: 0.7s ease;
          }
          input[type="text"]:focus,
          input[type="password"]:focus {
            box-shadow: inset 0 0 2px 1px rgba(0, 81, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
          }
        }
      }
      .login-footer {
        border-top: 2px solid rgba(255, 255, 255, 0.7);
        text-align: center;
        background-color: rgba(0, 0, 0, 0.7);
        padding: 10px 0 10px 0;
        cursor: pointer;
        user-select: none;
      }
    }
    @media screen and (max-width: 414px) {
      .login {
        width: 100%;
        min-width: 0;
      }
    }
  }
  .modalC {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    .modal-bodyC {
      position: absolute;
      top: 50%;
      left: 50%;
      border: 1px solid black;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
      background: white;
      border-radius: 10px;
      margin: 2% auto;
      min-width: 400px;
      opacity: 0;
      transform: translate(-50%, -50%) scale(0.1);
      transition: 0.5s ease;
    }
    .modal-body-toggleC {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
      transition: 0.5s ease;
    }
    .modal-headerC {
      display: flex;
      justify-content: space-between;
      padding: 10px 15px 10px 15px;
      border-bottom: 1px solid black;
      span {
        display: block;
        font-size: 20px;
        font-weight: bold;
      }
    }
    .modal-contentC {
      padding: 20px 50px 15px 50px;
      text-align: center;
    }
  }
  .modal-toggleC {
    opacity: 1;
  }
}
</style>
<script>
export default {
  data() {
    return {
      accountInput: "",
      passwordInput: "",
      accountGroup: [],
      modalCount: 3,
      loginStatus: false,
    };
  },
  methods: {
    showModal() {
      const modalShow = document.querySelector(".modalC");
      const modalContentShow = document.querySelector(".modal-bodyC");
      modalShow.style.display = "block";
      modalShow.style.transition = "1s ease";
      setTimeout(() => {
        modalShow.classList.add("modal-toggleC");
        modalContentShow.classList.add("modal-body-toggleC");
      }, 100);
      setTimeout(() => {
        let timmer = setInterval(() => {
          if (this.modalCount == 0) {
            clearInterval(timmer);
            this.closeModal(modalShow, modalContentShow);
            setTimeout(() => {
              this.modalCount = 3;
              this.loginStatus == true
                ? this.$router.push("/dashboard/allarticle")
                : this.$router.push("/");
            }, 990);
          } else {
            this.modalCount -= 1;
          }
        }, 1000);
      }, 1000);
    },
    closeModal(modalShow, modalContentShow) {
      modalShow.classList.remove("modal-toggleC");
      modalContentShow.classList.remove("modal-body-toggleC");
      setTimeout(() => (modalShow.style.display = ""), 750);
    },
    goArticleControlCenter() {
      this.accountGroup.forEach((key) =>
        (key.account == this.accountInput &&
          key.password == this.passwordInput) ||
        (this.accountInput != "" && this.passwordInput != "")
          ? (this.loginStatus = true)
          : (this.loginStatus = false)
      );
      setTimeout(() => this.showModal(), 100);
    },
    getAccountInformation() {
      this.axios
        .get("https://acwithps-20xx.firebaseio.com/.json")
        .then((res) => this.accountGroup.push(res.data))
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.getAccountInformation();
  },
};
</script>